.bl_accordion {
  position: relative;
  width: 100%;
  border-top: none;
  border-bottom: 1px solid #c4c4c4;
  outline: 0;
  cursor: pointer;
}

.bl_accordion .bl_accordion_title {
  display: block;
  position: relative;
  margin: 0;
  padding: 20px 40px 20px 20px;
  border-top: 1px solid #c4c4c4;
  font-size: 1.25em;
  font-weight: normal;
  cursor: pointer;
  transition: 0.3s;
}

.bl_accordion .bl_accordion_title:hover, .bl_accordion .bl_accordion_title:active {
  background-color: #ff993c;
  color: #fff;
}

.bl_accordion .bl_accordion_title:hover::after, .bl_accordion .bl_accordion_title:active::after {
  border-color: #fff;
}

.bl_accordion_title {
  position: relative;
}

.bl_accordion_title::after {
  display: block;
  position: absolute;
  top: 38%;
  right: 25px;
  transform: rotate(135deg);
  width: 8px;
  height: 8px;
  border-top: solid 2px #333;
  border-right: solid 2px #333;
  content: "";
  transition: all .2s ease-in-out;
}

.bl_accordion_title.open:after {
  top: 45%;
  transform: rotate(-45deg);
}

.bl_accordion_content {
  display: none;
  margin-bottom: 20px;
  margin-left: 40px;
  padding: 20px;
  background-color: #c4c4c4;
}

